import React from "react";
import { useLocation } from "react-router-dom";
import Certainty from "../Certainty/Certainty";
import Contact from "../Contact/Contact";
import DataPoints from "../DataPoints/DataPoints";
import DataUsable from "../DataUsable/DataUsable";
import EventIntel from "../EventIntel/EventIntel";

import Nodata from "../Nodata/Nodata";
import Pricing from "../Pricing/Pricing";
import ThreePoints from "../ThreePoints/ThreePoints";


const Home = () => {
	const location = useLocation();	
	const pricingSection = React.useRef(null);
	const contactSection = React.useRef(null);

	const goto = (section) => {
		switch(section) {
			case "#pricing":
				window.scroll({
					top: pricingSection.current.offsetTop,
					behavior: "smooth",
				});
			break;

			case "#contact":
				window.scroll({
					top: contactSection.current.offsetTop,
					behavior: "smooth",
				});
			break;

			default:
			return false;
		}

		return true;
	}

	React.useEffect(() => {
		const hash = location.hash;
		goto(hash);
	}, [location]);

  return (
		<div className="container-fluid px-0">
			<Certainty />

			{window.innerWidth > 768 && <Nodata />}

			<DataPoints />

			{window.innerWidth > 768 && <EventIntel />}

			<DataUsable />
			<ThreePoints />
      
			<div ref={pricingSection}>
				<Pricing />
			</div>

			<div ref={contactSection}>
				<Contact />
			</div>
    </div>
  );
};

export default Home;